@import '~@lumino/default-theme/style/index.css';
/*@import './content.css';*/

body {
  display: flex;
  flex-direction: column;
  position: absolute;
  background-color: #fbfbfb;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0;
  padding: 0;
}

#menuBar {
  flex: 0 0 auto;
}


#main {
  flex: 1 1 auto;
}

#palette {
  min-width: 300px;
  border-right: 1px solid #DDDDDD;
}

#dock {
  padding: 4px;
}

.CodeMirrorWidget {
  border: 1px solid #C0C0C0;
  min-width: 200px;
  min-height: 200px;
  width: 100%;
  height: 100%;
  margin: auto;
}

.AceEditorWidget, .CodeMirrorWidget > .CodeMirror  { 
  border: 0px;
  min-width: 200px;
  min-height: 200px;
  width: 100%!important;
  height: calc(100% - 36px)!important;
  margin: auto;
}

.scrollmargin {
  height: 80px;
  text-align: center;
}

.p-TabBar-content, .lm-TabBar-content {
    min-width: 0;
    min-height: 0;
    align-items: flex-end;
    border-bottom: 1px solid transparent;
    background-color: #dfdfdf;
}

.p-TabBar-tab, .lm-TabBar-tab {
    /*
    padding: 0px 10px;
    background: #E5E5E5;
    border: 1px solid #C0C0C0;
    border-bottom: none;
    font: 12px Helvetica, Arial, sans-serif;
    flex: 0 1 125px;*/
    min-height: 22px;
    max-height: 22px;
    /*
    min-width: 35px;
    margin-left: -1px;
    line-height: 20px;*/
}

.flow-select {
  width: 100%;
  padding: 2px;
  height: 24px;
  margin: auto;
  font-family: "Consolas";
}

.separator {
  width:100%;
  height: 1px;
  border-top: 1px solid lightgray;
}

.app {
  font-family: sans-serif;
  display: flex;
  padding: 8px;
}

.app-content-pane {
  flex: 1;
  height: 90vh;
  width: 90vw;
}